@import '/variables.scss';

@mixin test {
  border: 1px solid blue !important;
}

@mixin transition {
  transition: all $timing $easing;
}
@mixin transitionFast {
  transition: all $timing/2 $easing;
}

@keyframes navLinkCollapsed {
  0% {
  }
  50% {
    border-radius: $radius !important;
  }
  100% {
    padding: 0;
    border-radius: $radius !important;
    margin: 3px;
  }
}
@keyframes navLinkUnCollapsed {
  0% {
    padding: 0;
    border-radius: $radius !important;
    margin: 3px;
  }
  50% {
    border-radius: $radius !important;
  }
  100% {
  }
}
@keyframes navItemCollapsed {
  0% {
    opacity: 1;
  }
  55% {
    opacity: 0;
  }
  100% {
    opacity: 0;
    width: 6px;
    height: 6px;
  }
}
@keyframes navItemUnCollapsed {
  0% {
    opacity: 0;
    width: 6px;
    height: 6px;
  }
  55% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@mixin animation($duration, $delay, $animation) {
  animation: $duration $easing $delay 1 $animation forwards;
}


@mixin ellipsis($width) {
  width: $width;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@mixin uppercase {
  text-transform: uppercase;
  letter-spacing: 2px;
}

@mixin shadow {
  box-shadow: 0px 1px 15px 1px rgba(69,65,78,0.06);
}

@mixin shadowSmall {
  box-shadow: 0px 1px 8px 1px rgba(69, 65, 78, 0.15);
}

@mixin shadowPrimary {
  box-shadow: 0px 1px 15px 1px rgba($primaryColor,0.15);
}

@mixin container {
  padding: $buffer*6;
  margin-bottom: $buffer*3;
}

@mixin transform($transforms) {
	   -moz-transform: $transforms;
	     -o-transform: $transforms;
	    -ms-transform: $transforms;
	-webkit-transform: $transforms;
          transform: $transforms;
}

@mixin breakpoint($class) {
  @if $class == xs {
    @media (max-width: 767px) { @content; }
  }

  @else if $class == sm {
    @media (max-width: 768px) { @content; }
  }

  @else if $class == md {
    @media (max-width: 992px) { @content; }
  }

  @else if $class == lg {
    @media (min-width: 1200px) { @content; }
  }

  @else {
    @warn "Breakpoint mixin supports: xs, sm, md, lg";
  }
}
